/* 路由跳转 */
.slide-go-enter-active,.slide-go-leave-active,.slide-back-enter-active,.slide-back-leave-active{
  -webkit-transition: transform 0.4s cubic-bezier(.55,0,.1,1);
  -webkit-transition: -webkit-transform 0.4s cubic-bezier(.55,0,.1,1);
  transition: -webkit-transform 0.4s cubic-bezier(.55,0,.1,1);
  transition: transform 0.4s cubic-bezier(.55,0,.1,1);
  transition: transform 0.4s cubic-bezier(.55,0,.1,1), -webkit-transform 0.4s cubic-bezier(.55,0,.1,1);
   /* transition-delay: 0.5s; */
}
.slide-back-leave-to,.slide-go-enter{
  -webkit-transform: translate3d(100%,0,0);
          transform: translate3d(100%,0,0);
}
.slide-go-leave,.slide-go-enter-to,.slide-back-leave,.slide-back-enter-to{
  -webkit-transform:translate3d(0%,0,0);
          transform:translate3d(0%,0,0)
}
.slide-back-enter,.slide-go-leave-to{
  -webkit-transform:translate3d(-100%,0,0);
          transform:translate3d(-100%,0,0)
}


.top-bar {
  width: 100%;
  z-index: 3;
  background-color: #fff;
}
.top-bar .loading-bar {
    height: 2px;
    content: '';
}
.top-bar .loading-bar .loading {
      width: 0;
      height: 2px;
      background-color: #FF6B00;
      opacity: 0.7;
}
.top-bar .loading-bar .loading-enter-active {
      -webkit-animation: loading 0.4s cubic-bezier(1, 0.5, 0.1, 1);
              animation: loading 0.4s cubic-bezier(1, 0.5, 0.1, 1);
}
@-webkit-keyframes loading {
0% {
    width: 0;
}
99% {
    width: 100%;
}
100% {
    width: 0;
}
}
@keyframes loading {
0% {
    width: 0;
}
99% {
    width: 100%;
}
100% {
    width: 0;
}
}
